import { adapters } from "@rp/adapters"

export const planComparisonCustomerIo = {
  Modal: {
    open(source: string) {
      adapters.customerIo.track({
        name: "Open plan Comparison Modal",
        data: {
          action: "Open",
          source: source,
        },
      })
    },
    freePlan() {
      adapters.customerIo.track({
        name: "Pressed Free Plan Button ",
        data: {
          action: "click",
        },
      })
    },
    bookDemo(source: string) {
      adapters.customerIo.track({
        name: "Pressed Book Demo Button ",
        data: {
          action: "click",
          source: source,
        },
      })
    },
  },
}
